import React, { useState } from 'react';
import { Container, Typography, Box, Grid, Card, CardContent, CardMedia, CardActions, Button, useTheme, useMediaQuery, IconButton, MobileStepper } from '@mui/material';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';

//  
import agzibm from '../assets/agzibm--2.jpg';
import gpim from '../assets/gpim--2.jpg';
import pchl from '../assets/pchl--2.jpg';
import kish from '../assets/kish--2.jpg';
import shik from '../assets/shik--2.jpg';

import apelsin1 from '../assets/apelsin1.jpg';
import apelsin2 from '../assets/apelsin2.jpg';
import apelsin3 from '../assets/apelsin3.jpg';
import mozhzh2 from '../assets/mozhzh2.jpg';
import mozhzh3 from '../assets/mozhzh3.jpg';

import lavanda1 from '../assets/lavanda1.jpg';
import lavanda2 from '../assets/lavanda2.jpg';
import lavanda3 from '../assets/lavanda3.jpg';
import pihta2 from '../assets/pihta2.jpg';
import pihta3 from '../assets/pihta3.jpg';

import pryaniy1 from '../assets/pryaniy1.jpg';
import pryaniy2 from '../assets/pryaniy2.jpg';
import pryaniy3 from '../assets/pryaniy3.jpg';
import sinnabon2 from '../assets/sinnabon2.jpg';
import sinnabon3 from '../assets/sinnabon3.jpg';

import kokos1 from '../assets/kokos1.jpg';
import kokos2 from '../assets/kokos2.jpg';
import kokos3 from '../assets/kokos3.jpg';
import muskus2 from '../assets/muskus2.jpg';
import muskus3 from '../assets/muskus3.jpg';

import zeleniy1 from '../assets/zeleniy1.jpg';
import zeleniy2 from '../assets/zeleniy2.jpg';
import zeleniy3 from '../assets/zeleniy3.jpg';
import shafran3 from '../assets/shafran3.jpg';

function Catalog() {
  const theme = useTheme();
  const isDarkMode = theme.palette.mode === 'dark';
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [activeStep, setActiveStep] = useState({});

  const handleNext = (productId, totalSteps) => {
    setActiveStep(prev => ({
      ...prev,
      [productId]: (prev[productId] || 0) === totalSteps - 1 ? 0 : (prev[productId] || 0) + 1
    }));
  };

  const handleBack = (productId, totalSteps) => {
    setActiveStep(prev => ({
      ...prev,
      [productId]: (prev[productId] || 0) === 0 ? totalSteps - 1 : (prev[productId] || 0) - 1
    }));
  };

  const handleStepChange = (productId, step, totalSteps) => {
    if (step < 0) {
      step = totalSteps - 1;
    } else if (step >= totalSteps) {
      step = 0;
    }
    setActiveStep(prev => ({
      ...prev,
      [productId]: step
    }));
  };

  const products = [
    {
      id: 1,
      name: '     ",    ,   "',
      price: '1100 ',
      description: '       2  :    ,    ,      .',
      images: [agzibm, apelsin1, apelsin2, apelsin3, mozhzh2, mozhzh3]
    },
    {
      id: 2,
      name: '     " ,    "',
      price: '1050 ',
      description: '       2  :     ,       .',
      images: [gpim, lavanda1, lavanda2, lavanda3, pihta2, pihta3]
    },
    {
      id: 3,
      name: '     "  , "',
      price: '1050 ',
      description: '       2  :      ,    .',
      images: [pchl, pryaniy1, pryaniy2, pryaniy3, sinnabon2, sinnabon3]
    },
    {
      id: 4,
      name: '     "  ,   "',
      price: '1150 ',
      description: '       2  :      ,      .',
      images: [kish, kokos1, kokos2, kokos3, muskus2, muskus3]
    },
    {
      id: 5,
      name: '     "  ,      "',
      price: '1200 ',
      description: '       2  :      ,         .',
      images: [shik, zeleniy1, zeleniy2, zeleniy3, shafran3]
    }
  ];

  return (
    <Box sx={{ 
      minHeight: 'calc(100vh - 64px)',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      py: { xs: 4, sm: 6 },
      px: { xs: 2, sm: 4 },
      background: isDarkMode 
        ? 'linear-gradient(135deg, #121212 0%, #1a1a1a 100%)'
        : 'linear-gradient(135deg, #FFFFFF 0%, #f8f8f8 100%)'
    }}>
      <Container maxWidth="lg">
        <Typography 
          variant="h3" 
          component="h1" 
          gutterBottom 
          align="center"
          sx={{
            fontWeight: 700,
            color: isDarkMode ? '#DEB887' : '#8B4513',
            mb: isMobile ? 2 : 4,
            mt: isMobile ? 2 : { xs: 4, sm: 6, md: 8 },
            fontSize: isMobile ? '1.75rem' : { xs: '2rem', sm: '2.5rem', md: '3rem' }
          }}
        >
             
        </Typography>
        <Typography 
          variant="h6" 
          gutterBottom 
          align="center" 
          color="text.secondary"
          sx={{ 
            mb: 6,
            fontSize: { xs: '1rem', sm: '1.25rem' }
          }}
        >
                 
        </Typography>

        <Grid container spacing={3}>
          {products.map((product) => (
            <Grid item xs={12} sm={6} md={4} key={product.id}>
              <Card 
                sx={{
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  maxWidth: '400px',
                  margin: '0 auto',
                  background: isDarkMode 
                    ? 'rgba(30, 30, 30, 0.8)'
                    : 'rgba(255, 255, 255, 0.8)',
                  backdropFilter: 'blur(10px)',
                  border: `1px solid ${isDarkMode 
                    ? 'rgba(222, 184, 135, 0.1)'
                    : 'rgba(139, 69, 19, 0.1)'}`,
                  transition: 'transform 0.2s, box-shadow 0.2s',
                  '&:hover': {
                    transform: 'translateY(-4px)',
                    boxShadow: isDarkMode
                      ? '0 4px 20px rgba(222, 184, 135, 0.1)'
                      : '0 4px 20px rgba(139, 69, 19, 0.1)'
                  }
                }}
              >
                <Box sx={{ position: 'relative' }}>
                  <SwipeableViews
                    axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                    index={activeStep[product.id] || 0}
                    onChangeIndex={(step) => handleStepChange(product.id, step, product.images.length)}
                    enableMouseEvents
                    resistance
                    cycleNavigation
                  >
                    {product.images.map((image, index) => (
                      <Box key={index}>
                        <CardMedia
                          component="img"
                          height="300"
                          image={image}
                          alt={`${product.name} -  ${index + 1}`}
                          sx={{
                            objectFit: 'contain',
                            background: isDarkMode 
                              ? 'rgba(0, 0, 0, 0.2)'
                              : 'rgba(255, 255, 255, 0.2)',
                            padding: '16px',
                            borderBottom: `1px solid ${isDarkMode 
                              ? 'rgba(222, 184, 135, 0.1)'
                              : 'rgba(139, 69, 19, 0.1)'}`
                          }}
                        />
                      </Box>
                    ))}
                  </SwipeableViews>
                  <MobileStepper
                    steps={product.images.length}
                    position="static"
                    activeStep={activeStep[product.id] || 0}
                    sx={{
                      background: 'transparent',
                      padding: '8px',
                      '& .MuiMobileStepper-dot': {
                        backgroundColor: isDarkMode 
                          ? 'rgba(222, 184, 135, 0.3)'
                          : 'rgba(139, 69, 19, 0.3)',
                      },
                      '& .MuiMobileStepper-dotActive': {
                        backgroundColor: isDarkMode ? '#DEB887' : '#8B4513',
                      }
                    }}
                    nextButton={
                      <IconButton 
                        size="small" 
                        onClick={() => handleNext(product.id, product.images.length)}
                        sx={{ 
                          color: isDarkMode ? '#DEB887' : '#8B4513',
                          '&.Mui-disabled': {
                            color: isDarkMode 
                              ? 'rgba(222, 184, 135, 0.3)'
                              : 'rgba(139, 69, 19, 0.3)',
                          }
                        }}
                      >
                        <KeyboardArrowRight />
                      </IconButton>
                    }
                    backButton={
                      <IconButton 
                        size="small" 
                        onClick={() => handleBack(product.id, product.images.length)}
                        sx={{ 
                          color: isDarkMode ? '#DEB887' : '#8B4513',
                          '&.Mui-disabled': {
                            color: isDarkMode 
                              ? 'rgba(222, 184, 135, 0.3)'
                              : 'rgba(139, 69, 19, 0.3)',
                          }
                        }}
                      >
                        <KeyboardArrowLeft />
                      </IconButton>
                    }
                  />
                </Box>
                <CardContent sx={{ flexGrow: 1, py: 1.5 }}>
                  <Typography 
                    gutterBottom 
                    variant="h6" 
                    component="h2"
                    sx={{ 
                      color: isDarkMode ? '#DEB887' : '#8B4513',
                      fontWeight: 600,
                      fontSize: { xs: '0.95rem', sm: '1.1rem' },
                      mb: 1
                    }}
                  >
                    {product.name}
                  </Typography>
                  <Typography 
                    variant="subtitle1" 
                    color="primary"
                    sx={{ 
                      mb: 1,
                      color: isDarkMode ? '#E6D5C3' : '#A0522D',
                      fontSize: { xs: '0.9rem', sm: '1rem' },
                      fontWeight: 600
                    }}
                  >
                    {product.price}
                  </Typography>
                  <Typography 
                    variant="body2" 
                    color="text.secondary"
                    sx={{ 
                      mb: 1,
                      fontSize: { xs: '0.8rem', sm: '0.9rem' },
                      lineHeight: 1.4
                    }}
                  >
                    {product.description}
                  </Typography>
                </CardContent>
                <CardActions sx={{ 
                  display: 'flex', 
                  flexDirection: 'column', 
                  gap: 1,
                  mt: 'auto',
                  p: 2,
                  width: '100%',
                  alignItems: 'center',
                  '& .MuiButton-root': {
                    width: '100%',
                    borderRadius: '8px',
                    marginLeft: 0
                  }
                }}>
                  <Button 
                    variant="contained"
                    sx={{
                      width: '100%',
                      height: '48px',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      fontSize: '0.875rem',
                      background: isDarkMode
                        ? 'linear-gradient(45deg, #DEB887 30%, #E6D5C3 90%)'
                        : 'linear-gradient(45deg, #8B4513 30%, #A0522D 90%)',
                      color: isDarkMode ? '#121212' : '#FFFFFF',
                      '&:hover': {
                        background: isDarkMode
                          ? 'linear-gradient(45deg, #E6D5C3 30%, #DEB887 90%)'
                          : 'linear-gradient(45deg, #A0522D 30%, #8B4513 90%)',
                      },
                    }}
                  >
                      Wildberries
                  </Button>
                  <Button 
                    variant="contained"
                    sx={{
                      width: '100%',
                      height: '48px',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      fontSize: '0.875rem',
                      background: isDarkMode
                        ? 'linear-gradient(45deg, #DEB887 30%, #E6D5C3 90%)'
                        : 'linear-gradient(45deg, #8B4513 30%, #A0522D 90%)',
                      color: isDarkMode ? '#121212' : '#FFFFFF',
                      '&:hover': {
                        background: isDarkMode
                          ? 'linear-gradient(45deg, #E6D5C3 30%, #DEB887 90%)'
                          : 'linear-gradient(45deg, #A0522D 30%, #8B4513 90%)',
                      },
                    }}
                  >
                      Ozon
                  </Button>
                  <Button 
                    variant="contained"
                    sx={{
                      width: '100%',
                      height: '48px',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      fontSize: '0.875rem',
                      background: isDarkMode
                        ? 'linear-gradient(45deg, #DEB887 30%, #E6D5C3 90%)'
                        : 'linear-gradient(45deg, #8B4513 30%, #A0522D 90%)',
                      color: isDarkMode ? '#121212' : '#FFFFFF',
                      '&:hover': {
                        background: isDarkMode
                          ? 'linear-gradient(45deg, #E6D5C3 30%, #DEB887 90%)'
                          : 'linear-gradient(45deg, #A0522D 30%, #8B4513 90%)',
                      },
                    }}
                  >
                      .
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
}

export default Catalog; 